import * as React from "react";
import { Button, ButtonType } from "office-ui-fabric-react";
import Header from "./Header";
import HeroList, { HeroListItem } from "./HeroList";
import Progress from "./Progress";
/* global Button, Header, HeroList, HeroListItem, Progress */
const POWERAPPURL =
  "https://apps.powerapps.com/play/11895e09-d2f9-459b-ac3f-aa3b222d9ab1?tenantId=79dc228f-c8f2-4016-8bf0-b990b6c72e98?id=";

function get(property) {
  return new Promise((resolve, reject) => {
    property.getAsync(result => {
      if (result.status !== Office.AsyncResultStatus.Succeeded) {
        return reject(result.error);
      }
      return resolve(result.value);
    });
  });
}

function getLocation() {
  return get(Office.context.mailbox.item.location);
}
function getOrganizer(){
  return get(Office.context.mailbox.item.organizer)
}
function getBody() {
  return new Promise((resolve, reject) => {
    Office.context.mailbox.item.body.getAsync(Office.CoercionType.Text, function(asyncResult) {
      if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
        resolve(asyncResult.value);
      } else {
        reject(asyncResult.error);
      }
    });
  });
}

function insertRemyLink(id) {
  return new Promise((resolve, reject) => {
    try {
      var html = "<div style='color:#005776'> <hr/><h2>REMY - Nets Meeting Services</h2>";
      html +=
        "This is a link used by the organizer of the meeting to order additional services<br/><br/><a href='" +
        POWERAPPURL +
        id +
        "' target='_blank'>Open REMY</a><br/><hr/></div>";

      Office.context.mailbox.item.setSelectedDataAsync(
        html,
        {
          coercionType: Office.CoercionType.Html
        },
        function(asyncResult) {
          if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
            resolve("Selected text has been updated successfully.");
          } else {
            reject(asyncResult.error);
          }
        }
      );
    } catch (error) {
      reject(error);
    }
  });
}
export default class App extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      listItems: [],
      logs: ["Log"],
      isConnected: false
    };
  }

  doLoad = async () => {
//    this.log("doload");

    var body = await getBody().catch(error => {
      this.log("ERROR: " + error);
    });
    if (body && body.indexOf(POWERAPPURL) > -1) {
      this.setState({ isConnected: true });
    }
  };

  componentDidMount() {
//    this.log("mounted");
    this.doLoad();
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.isOfficeInitialized !== this.props.isOfficeInitialized) {
//      this.log("updated");
      this.doLoad();
    }
  }

  log = entry => {
    this.state.logs.push(entry);
    this.setState({ logs: this.state.logs });
  };
  click = async () => {
    try {

      var organizer = await getOrganizer()
      var timestamp = Date.now()
      var id = organizer.emailAddress + ":" + timestamp
      var body = await getBody().catch(error => {
        this.log("ERROR: " + error);
      });
      this.log(body);

      if (body && body.indexOf(POWERAPPURL) === -1) {
        var insertStatus = await insertRemyLink(id).catch(error => {
          this.log("ERROR: " + error);
        });
        if (insertStatus) this.setState({ isConnected: true });
      } else {
        this.setState({ isConnected: true });
        this.log("Already linked");
      }
    } catch (error) {
      this.log("ERROR" + error.message);
    }
  };

  render() {
    const { title, isOfficeInitialized } = this.props;

    if (!isOfficeInitialized) {
      return <Progress title={title} logo="assets/remy.jpg" message="Please sideload your addin to see app body." />;
    }

    return (
      <div className="ms-welcome">
        <Header logo="assets/remy.jpg" title={this.props.title} message="REMY" />
        <HeroList items={this.state.listItems}>
          {!this.state.isConnected && (
            <div style={{width:"100vw",padding:"10px"}}>
            <p>
            REMY can assist you in ordering additional services and report Visitors to the reception.
            In order to REMY to know where to deliver the services, and where to expect the visitors, you need to have a room specified
            </p>
            <Button
              className="ms-welcome__action"
              buttonType={ButtonType.hero}
              xiconProps={{ iconName: "ChevronRight" }}
              onClick={this.click}
            >
              Connect Appointment
            </Button>
            </div>
          )}
          {this.state.isConnected && <div><h1>Connected</h1>Keep the REMY link within your appointment</div>}
        </HeroList>
        <div>
          {this.state &&
            this.state.logs &&
            this.state.logs.map((entry, key) => {
              return <div key={key}>{entry}</div>;
            })}
        </div>
      </div>
    );
  }
}
